import Vue from 'vue'
import store from '../store'
 import App from './App.vue'
import VueRouter from 'vue-router'
 import Etapa from './components/Etapa'
 import Montando from './components/Montando'
import Recheio from './components/Recheio'
import Checkout  from './components/Checkout'
 Vue.use(VueRouter)

const router = new VueRouter({
 
  routes : [

    {
      path: '/',
       component: Etapa
    },
    {
      path: '/montando',
      component: Montando
    },
    {
      path: '/recheio',
      component: Recheio
    },
    {
      path: '/Checkout',
      component: Checkout
    },
   
   
   
    
  ]
})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
