import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        nome2:"",
        valor:"",
        tamanho:"",
        valorTam:"",
        recheio:"",
        valorRecheio:"",
        top:""
     
     },
    getters:{
        teste(state){
            return state.nome2
        },
        valor(state){
            return state.valor
        },
        tamanho(state){
            return state.tamanho
        },
        valorTa(state){
            return state.valorTam
        },
        recheioPizza(state){
            return state.recheio
        },
        valorfinal(state){
            return state.valorRecheio
        },
       top(state){
           return state.top
       }
    },
    mutations:{
        ola(state){
            let valorfinal = document.querySelector(".result1").innerHTML
            let valorfinal2 = document.querySelector(".result2").innerHTML
            let valorfinal3 = document.querySelector(".result3").innerHTML
            let o = parseInt(valorfinal)
            let op = parseInt(valorfinal2)
            let opp = parseInt(valorfinal3)
            let soma = o + op + opp
             let pou = document.querySelector(".finalk").innerHTML = soma
               state.top = pou
      },
        mudando(state){
            let check1 = document.querySelector(".check1")
            let check2 = document.querySelector(".check2")
            let check3 = document.querySelector(".check3")

             if(check1.checked){
                state.nome2 = "Napolitana";
            }
            if(check2.checked){
                state.nome2 = "Nova-iorquina";
            }
            if(check3.checked){
                state.nome2 = "Siciliana"
            }
               
         },
         valor(state){
            let check1 = document.querySelector(".check1")
            let check2 = document.querySelector(".check2")
            let check3 = document.querySelector(".check3")

             if(check1.checked){
                state.valor = "10"

            }
            if(check2.checked){
                state.valor = "12"

            }
            if(check3.checked){
                state.valor = "14"
            }
         },
         tamanho(state){
            let check1 = document.querySelector(".check1")
            let check2 = document.querySelector(".check2")
            let check3 = document.querySelector(".check3")

             if(check1.checked){
                state.tamanho = "Pequena"

            }
            if(check2.checked){
                state.tamanho = "Média"

            }
            if(check3.checked){
                state.tamanho = "Grande"
            }
         },
         valorTam(state){
            let check1 = document.querySelector(".check1")
            let check2 = document.querySelector(".check2")
            let check3 = document.querySelector(".check3")

             if(check1.checked){
                state.valorTam = "8"

            }
            if(check2.checked){
                state.valorTam = "10"

            }
            if(check3.checked){
                state.valorTam = "12"
            }
         },
         recheio(state){
            let check1 = document.querySelector(".check1")
            let check2 = document.querySelector(".check2")
            let check3 = document.querySelector(".check3")
            let check4 = document.querySelector(".check4")
            let check5 = document.querySelector(".check5")

             if(check1.checked){
                state.recheio = "Napolitana"

            }
            if(check2.checked){
                state.recheio = "Marguerita"

            }
            if(check3.checked){
                state.recheio = "Calabresa"
            }
            if(check4.checked){
                state.recheio = "Frango com Catupiry"
            }
            if(check5.checked){
                state.recheio = "Quatro queijos"
            }
    },
    valorRecheio(state){
        let check1 = document.querySelector(".check1")
        let check2 = document.querySelector(".check2")
        let check3 = document.querySelector(".check3")
        let check4 = document.querySelector(".check4")
        let check5 = document.querySelector(".check5")

         if(check1.checked){
            state.valorRecheio = "8"

        }
        if(check2.checked){
            state.valorRecheio = "10"

        }
        if(check3.checked){
            state.valorRecheio = "12"
        }
        if(check4.checked){
            state.valorRecheio = "14"
        }
        if(check5.checked){
            state.valorRecheio = "16"
        }
}}
})